import {useState} from 'react'

const Pool = ({poolinfo, b0, onMint, onApprove, onAddLiquidity, onRemoveLiquidity, onAddMargin, onRemoveMargin}) => {
    const [vusdt, setVUsdt] = useState(0)
    return (
        <div>
            <h3>Pool Info</h3>
            <table>
            <tbody>
                <tr>
                    <th></th>
                    <th>USDT</th>
                    <th>Protocol Fee Accrued</th>
                </tr>
                <tr>
                    <th>Pool</th>
                    <td>{poolinfo.balanceUSDT}</td>
                    <td>{poolinfo.protocolFeeAccrued}</td>
                </tr>
                <tr>
                    <th>User</th>
                    <td>{b0.user_balance}</td>
                    <td></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onMint(0)}>Mint USDT</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onApprove(0)}>Approve USDT</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><input type='text' value={vusdt} onChange={(e) => setVUsdt(e.target.value)} /></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onAddLiquidity(0, vusdt)}>AddLiquidity USDT</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onRemoveLiquidity(0, vusdt)}>RemoveLiquidity USDT</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onAddMargin(0, vusdt)}>AddMargin USDT</button></td>
                </tr>
                <tr>
                    <td/>
                    <td><button onClick={() => onRemoveMargin(0, vusdt)}>RemoveMargin USDT</button></td>
                </tr>
            </tbody>
            </table>
        </div>
    )
}

export default Pool
