import {useState, useEffect} from 'react'
import Web3 from 'web3'
import abis from './utils/abis'

import Pool from './components/Pool'
import Symbols from './components/Symbols'
import BTokens from './components/BTokens'
import Liquidation from './components/Liquidation'

function fromWei(value, unit='ether') {
    return Web3.utils.fromWei(value, unit)
}

function toWei(value, unit='ether') {
    return Web3.utils.toWei(value, unit)
}

function App() {

    const web3 = new Web3(window.ethereum)

    const usdt = new web3.eth.Contract(abis.erc20, '0x1BD7B233B054AD4D1FBb767eEa628f28fdE314c6')

    const ltoken = new web3.eth.Contract(abis.ltoken, '0xeFCbBF65100FC2C2b96E92c9AbFfeC384177943e')
    const ptoken = new web3.eth.Contract(abis.ptoken, '0x3495f770eC0dc8701d2b89454047A8521306E070')
    const router = new web3.eth.Contract(abis.router, '0x4861a9958B65eA8146B89Af929a80c352c905270')
    const pool   = new web3.eth.Contract(abis.pool,   '0x35a85396e7A8a9E85170fbb589ce085abcAd8266')

    const [data, setData] = useState({
        account: '',
        poolinfo: {},
        symbol0: {},
        btoken0: {},
    })

    useEffect(() => {
        update()
    }, [])

    const update = async () => {
        const account = await getAccount()
        const poolinfo = await getPool()
        const symbol0 = await getSymbol(account, 0)
        const btoken0 = await getBToken(account, 0)
        setData({account, poolinfo, symbol0, btoken0})
    }

    const getAccount = async () => {
        return Web3.utils.toChecksumAddress((await window.ethereum.request({method: 'eth_requestAccounts'}))[0])
    }

    const getPool = async () => {
        return {
            balanceUSDT: fromWei(await usdt.methods.balanceOf(pool.options.address).call()),
            protocolFeeAccrued: fromWei(await pool.methods.getProtocolFeeAccrued().call()),
        }
    }

    const getSymbol = async (address, symbolId) => {
        const s = await pool.methods.getSymbol(symbolId).call()
        const p = await ptoken.methods.getPosition(address, symbolId).call()
        return {
            symbol: s.symbol,
            price: fromWei(s.price),
            cumulativeFundingRate: fromWei(s.cumulativeFundingRate),
            tradersNetVolume: fromWei(s.tradersNetVolume),
            tradersNetCost: fromWei(s.tradersNetCost),
            user_volume: fromWei(p.volume),
            user_cost: fromWei(p.cost),
            user_lastCumulativeFundingRate: fromWei(p.lastCumulativeFundingRate),
        }
    }

    const getBTokenFromId = (bTokenId) => {
        if (bTokenId === 0) return usdt
    }

    const getBToken = async (address, bTokenId) => {
        const b = await pool.methods.getBToken(bTokenId).call()
        const l = await ltoken.methods.getAsset(address, bTokenId).call()
        return {
            symbol: await getBTokenFromId(bTokenId).methods.symbol().call(),
            price: fromWei(b.price),
            liquidity: fromWei(b.liquidity),
            pnl: fromWei(b.pnl),
            cumulativePnl: fromWei(b.cumulativePnl),
            user_balance: fromWei(await getBTokenFromId(bTokenId).methods.balanceOf(address).call()),
            user_liquidity: fromWei(l.liquidity),
            user_pnl: fromWei(l.pnl),
            user_lastCumulativePnl: fromWei(l.lastCumulativePnl),
            user_margin: fromWei(await ptoken.methods.getMargin(address, bTokenId).call()),
        }
    }

    const onMint = async (id) => {
        if (id === 0) {
            await usdt.methods.mint(data.account, '10000000000000000000000').send({from: data.account})
        }
        await update()
    }

    const onApprove = async (id) => {
        const max = '0xffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff'
        if (id === 0) {
            await usdt.methods.approve(pool.options.address, max).send({from: data.account})
        }
    }

    const onAddLiquidity = async (id, value) => {
        await router.methods.addLiquidity(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onRemoveLiquidity = async (id, value) => {
        await router.methods.removeLiquidity(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onAddMargin = async (id, value) => {
        await router.methods.addMargin(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onRemoveMargin = async (id, value) => {
        await router.methods.removeMargin(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onTrade = async (id, value) => {
        await router.methods.trade(id, toWei(value)).send({from: data.account})
        await update()
    }

    const onLiquidate = async (address) => {
        await router.methods.liquidate(address).send({from: data.account})
        await update()
    }

    return (
        <div className="App">
            <h1>DeriV2 Mumbai</h1>
            <Pool
                poolinfo={data.poolinfo}
                b0={data.btoken0}
                onMint={onMint}
                onApprove={onApprove}
                onAddLiquidity={onAddLiquidity}
                onRemoveLiquidity={onRemoveLiquidity}
                onAddMargin={onAddMargin}
                onRemoveMargin={onRemoveMargin}
                onTrade={onTrade}
                onLiquidate ={onLiquidate}
            />
            <Symbols
                s0={data.symbol0}
                onTrade={onTrade}
            />
            <BTokens
                b0={data.btoken0}
            />
            <Liquidation onLiquidate={onLiquidate} />
        </div>
    )

}

export default App

